.login {
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 500px;
}

.head {
  margin-bottom: 1.5rem;
  font-size: 2rem;
  line-height: 1.2;
  color: var(--light);
  text-shadow: 0 0 4px var(--dark);
}

.body {
  display: flex;
  flex-direction: column;
}

.field {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: .5rem;
}

.right {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.field:last-child {
  margin-bottom: 0;
}

.textInput {
  box-sizing: border-box;
  width: 100%;
  height: 2rem;
  padding: .2rem .3rem;
  background-color: var(--light);
  font-size: 1rem;
  line-height: 1.4rem;
  color: var(--dark);
  border: .1rem solid var(--gray);
  border-radius: 0;
  outline: 0;
}

.textInput:focus {
  border-color: var(--red);
  box-shadow: 0 0 4px var(--red);
}

.submitButton {
  box-sizing: border-box;
  height: 2rem;
  margin-left: .5rem;
  padding: .3rem .8rem;
  font-size: 1rem;
  line-height: 1.4rem;
  color: var(--light);
  background-color: var(--red);
  border: 0;
  border-radius: 0;
  outline: 0;
  transition: all .2s ease;
}

.submitButton:hover {
  box-shadow: 0 0 4px var(--red);
}

.submitButton:active {
  background-color: var(--red-dark);
}

.textInput:disabled, .submitButton:disabled {
  opacity: .5;
}

.link {
  font-size: 1rem;
  line-height: 1.2;
  color: var(--light);
  text-decoration: none;
  text-shadow: 0 0 4px var(--dark);
  transition: all .2s ease;
}

.link:hover {
  color: var(--red);
}

.error {
  margin-top: .5rem;
  font-size: 1.2rem;
  line-height: 1.2;
  color: var(--danger);
  text-shadow: 0 0 4px var(--dark);
}

@media (max-width: 500px) {
  .field {
    flex-direction: column;
    align-items: unset;
  }

  .submitButton {
    margin-left: 0;
  }
}